

.search-container {
  display: flex;
  flex-direction: column;
 
}

.FactGrid-div { // box styling the factgrid title
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
}
  
.FactGridTitle-div {  //font of the title FactGrid
    padding-left:10px;
    color:#AD1457;
    font-weight: 600;
    font-size: 60px;
  }


.search-label { // style of the labels of the items
  font-weight: bold;
}

.search-itemSelected { // put a margin on top of the box of selected items
  margin-top: 50px;
}


//style the card of selected items

:host {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}


 

.selectedItemSize {
   font-size: 14px;
}


// Réduit l’interligne entre les deux mat-hint
mat-form-field .mat-hint {
  margin-top: 0 !important;
  margin-bottom: 0 !important;
  line-height: 0.5 !important;
  padding: 0 !important;
}


// Affine le style du label de la checkbox (ligne déjà présente)
mat-hint mat-checkbox .mat-checkbox-layout .mat-checkbox-label {
  font-size: 0.75rem !important;
  font-weight: normal !important;
  line-height: 0.5 !important;
  padding: 0 !important;
  margin: 0 !important;
}











